___CSS_LOADER_EXPORT___.push([module.id, `.settings-dialog .dialog_text {
    font-size: x-large;
    font-style: italic;
}

.settings-dialog .section {
    display: flex;
}

.settings-dialog [data-section] {
    position: relative;
    min-width: max-content;
    margin: 2rem 0 0 1rem;
    padding: 1rem;
    height: max-content;
    border: 1px solid black;
    border-radius: 8px;
    color: hsl(  0deg   0%   0% / 100%);
    background-color: hsl(  0deg   0%  95% / 100%);
}

.settings-dialog [data-section]::before {
    position: absolute;
    content: attr(data-section);
    left: 0.8rem;
    top: -0.7rem;
    padding: 0 1rem;
    border: 1px solid #aaa;
    border-radius: 4px;
    color: hsl(  0deg   0%   0% / 100%);
    background-color: hsl(  0deg   0%  95% / 100%);
    font-style: italic;
}

.settings-dialog [data-section] [data-setting] label {
    display: inline-block;
    width: 9rem;
    text-align: right;
    font-family: sans-serif;
    font-size: small;
    user-select: none;
}

.settings-dialog [data-section] [data-setting] input,
.settings-dialog [data-section] [data-setting] select {
    color: hsl(  0deg   0%   0% / 100%);
    background-color: hsl(  0deg   0%  96% / 100%);
    margin: 0.25rem;
}

.settings-dialog [data-section] [data-setting] input[type="text"] {
    width: 5rem;
}

.settings-dialog .warning {
    display: none;
    width: 18rem;
    margin: 2rem 0 0 1rem;
    padding: 1rem;
    height: max-content;
    border: 1px solid black;
    border-radius: 8px;
    color: hsl(  0deg   0%   0% / 100%);
    background-color: hsl( 60deg  80%  50% / 100%);
}
.settings-dialog .warning p:first-child {
    margin-block-start: 0;
}
.settings-dialog .warning p:last-child {
    margin-block-end: 0;
}
.settings-dialog .show-emacs-warning .emacs-warning {
    display: block;
}

.settings-dialog input[type="submit"] {
    margin-top: 1rem;
}

.settings-dialog .error-message {
    display: none;
    margin: 2rem 0 0 1rem;
    max-width: 25vx;
    padding: 0.5rem;
    border-radius: 5pt;
    background-color: hsl(  0deg  60%  50% / 100%);
    color: hsl(  0deg   0% 100% / 100%);
    height: fit-content;
}
.settings-dialog .error-message.active {
    display: block;
}
